import * as React from 'react';
import styled, { ThemeProvider } from 'styled-components';

import { compose } from '../src';

const spacing = props => {
  const padding = props?.padding ?? props?.p;
  const paddingTop =
    props?.paddingVertical ?? props?.pv ?? props?.paddingTop ?? props?.pt;
  const paddingBottom =
    props?.paddingVertical ?? props?.pv ?? props?.paddingBottom ?? props?.pb;
  const paddingLeft =
    props?.paddingHorizontal ?? props?.ph ?? props?.paddingLeft ?? props?.pl;
  const paddingRight =
    props?.paddingHorizontal ?? props?.ph ?? props?.paddingRight ?? props?.pr;
  const margin = props?.margin ?? props?.m;
  const marginTop =
    props?.marginVertical ?? props?.mv ?? props?.marginTop ?? props?.mt;
  const marginBottom =
    props?.marginVertical ?? props?.mv ?? props?.marginBottom ?? props?.mb;
  const marginLeft =
    props?.marginHorizontal ?? props?.mh ?? props?.marginLeft ?? props?.ml;
  const marginRight =
    props?.marginHorizontal ?? props?.mh ?? props?.marginRight ?? props?.mr;
  return props => ({
    padding,
    paddingTop,
    paddingBottom,
    paddingLeft,
    paddingRight,
    margin,
    marginTop,
    marginBottom,
    marginLeft,
    marginRight,
  });
};

const Button = styled.button(
  compose(
    props => ({
      backgroundColor: props.theme.colors.primary,
      color: props.theme.colors.primaryColor,
      paddingLeft: props.theme.spacing.l,
      paddingRight: props.theme.spacing.l,
      paddingTop: props.theme.spacing.m,
      paddingBottom: props.theme.spacing.m,
    }),
    () => ({
      fontSize: 20,
      borderRadius: 5,
      border: 'none',
    }),
    spacing,
  ),
);

const theme = {
  colors: { primary: 'purple', primaryColor: 'white' },
  spacing: {
    xs: 2,
    s: 4,
    m: 8,
    l: 12,
    xl: 16,
  },
};

export default () => {
  return (
    <ThemeProvider theme={theme}>
      <div className="App">
        <h1>Hello CodeSandbox</h1>
        <h2>Start editing to see some magic happen!</h2>
        <Button ph={14}>Get started</Button>
      </div>
    </ThemeProvider>
  );
};
